@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');



*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container{
    max-width: 768px;
    margin: auto;
    padding: 0 20px; 
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: #202142;
    color: #fff;
    line-height: 1.6;
}

.head {
    font-size: large;
    text-align: center;
    background-color: yellow;
    color: purple;
    font-size: 26px;
    font-family: poppin;
    font-weight: 700;
    margin-top: 30px;
    margin-bottom: 50px;
    
  }
        
.card{
    background-color: #fff;
    color: #333;
    border-radius: 15px;
    padding: 40px 50px;
    margin: 20px 0;
    position: relative;
}
