.reviewitem_test{
    font-family: poppin;
    font-weight: 600;
    font-size: 22px;
}


 .num-display {
        position: absolute;
        top: -15px;
        left:-5px;
        text-align: center;
        background-color: #FF6A95;
        border-radius: 50%;
        width: 15%;
        height: 30%;
        font-size: 26px;
    }
      @media(min-width:425px){
        .num-display{
            width:8%;
            height:50%;  
        }
        
        .reviewitem_test{
            font-size: 32px; 
        }

     }

     .close,
     .edit{
        position: absolute;
        top: 10px;
        right: 20px;
        cursor: pointer;
        background: none;
        border: none;
     }
     .review-stats{
        color:#fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
     }
     .input-group{
        display: flex;
        flex-direction: row;
        border:1px solid #ccc;
        padding: 8px 10px;
        border-radius: 8px;
     }
     input{
        flex-grow: 2;
        border: none;
        font-size: 16px;
     }
     input:focus{
        outline: none;
     }
     .input-grow input{
        width: 80%;
     }
     .btn{
        color: #fff;
        border: 0;
        border-radius: 8px;
        width: 100px;
        height: 40px;
        cursor: pointer;
     }
     .btn-primary{
        background-color: #202142;
     }
     .btn-secondary{
        background: #FF6A95;
     }
     .btn:hover{
        transform: scale(0.98);
        opacity: 0.9;
     }
     .btn:disabled{
        background-color: #cccccc;
        color: #333;
        cursor: auto;
     }
     .btn:disabled:hover{
       transform: scale(1);
       opacity: 1;
     }
     .message{
      padding-top: 10px;
      text-align: center;
      color: rebeccapurple;
     }
     .rating{
      display: flex;
      align-items: center;
      justify-content: space-around;
      margin: 30px 0 40px;
     }
     li{
      display: flex;
     }
     .rating li,
     .num-display {
       position: relative;
       background: #f4f4f4;
       width: 50px;
       height: 50px;
       padding: 10px;
       text-align: center;
       border-radius: 50%;
       font-size: 19px;
       border: 1px #eee solid;
       transition: 0.3s;
     }
     .rating li label {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 50px;
      height: 50px;
      padding: 10px;
      border-radius: 50%;
      transform: translate(-50%, -50%);
      cursor: pointer;
     }
     .rating li:hover,
     .num-display {
       background: #ff6a95;
       color: #fff;
     }
     [type='radio'] {
      opacity: 0;
     }
     [type='radio']:checked ~ label {
      background: #ff6a95;
      color: #fff;
     }


    


